@keyframes rotateReversed {
  to {
    transform: rotate3d(0, 0, 1, -360deg);
  }
}
@keyframes rotate {
  to {
    transform: rotate3d(0, 0, 1, -20deg);
  }
}
@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade-out-bottom {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateY(50px);
    transform: translateY(50px);
    opacity: 0;
  }
}
@keyframes fade-in-bottom {
  0% {
    -webkit-transform: translateY(50px);
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes scale-out-center {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
    opacity: 1;
  }
}
@keyframes scale-in-center {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
}
header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99;
}
header .nav-menu {
  width: 100vw;
  padding: 30px 45px;
  height: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 400ms ease-in-out;
  will-change: transform, background-color, box-shadow, height;
}
@media (max-width: 1440px) {
  header .nav-menu {
    padding: 25px 35px;
    height: 80px;
  }
}
@media (max-width: 1366px) {
  header .nav-menu {
    padding: 20px 35px;
    height: 65px;
  }
}
@media (max-width: 1200px) {
  header .nav-menu {
    padding: 20px 20px;
  }
}
header .nav-menu .left_menu {
  display: flex;
  justify-content: space-between;
  gap: 60px;
}
@media (max-width: 1440px) {
  header .nav-menu .left_menu {
    gap: 30px;
  }
}
@media (max-width: 1440px) {
  header .nav-menu .left_menu .header__logo svg {
    width: 140px;
  }
}
@media (max-width: 1200px) {
  header .nav-menu .left_menu .header__logo svg {
    width: 120px;
  }
}
header .nav-menu .left_menu .header__logo .header-logo {
  fill: var(--white-pure);
}
header .nav-menu .left_menu ul.main_ul {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 70px;
}
@media (max-width: 1440px) {
  header .nav-menu .left_menu ul.main_ul {
    gap: 33px;
  }
}
@media (max-width: 1200px) {
  header .nav-menu .left_menu ul.main_ul {
    gap: 18px;
  }
}
header .nav-menu .left_menu ul.main_ul li {
  text-align: center;
  display: inline-block;
}
header .nav-menu .left_menu ul.main_ul li a {
  position: relative;
  z-index: 1;
  color: var(--white-pure);
  text-transform: uppercase;
  font-family: "Montserrat SemiBold";
  transition: color 0.4s ease-in-out;
}
@media (max-width: 1440px) {
  header .nav-menu .left_menu ul.main_ul li a {
    padding: 0 12px;
    font-size: 0.72rem;
  }
}
header .nav-menu .left_menu ul.main_ul li a.current {
  color: var(--black-80pc);
  transition: color 0.4s ease-in-out;
}
header .nav-menu .left_menu ul.main_ul li .main_submenu {
  opacity: 0;
  pointer-events: none;
  transition: all 0.4s cubic-bezier(0.93, 0.88, 0.1, 0.8);
  visibility: hidden;
  box-shadow: 0px 18px 20px -20px rgba(115, 115, 115, 0.8);
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu {
  position: absolute;
  width: 100%;
  background-color: #FFFFFF;
  right: 0;
  transition: all 0.4s cubic-bezier(0.93, 0.88, 0.1, 0.8);
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .submenu_slider {
  padding-top: 100px;
  padding-left: 20px;
  padding-bottom: 60px;
}
@media (-webkit-max-device-pixel-ratio: 2) and (max-width: 1540px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .submenu_slider {
    padding-top: 60px;
    padding-bottom: 40px;
  }
}
@media (max-width: 1440px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .submenu_slider {
    padding: 50px 0px 50px 20px;
  }
}
@media (max-width: 1366px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .submenu_slider {
    padding: 40px 0px 40px 20px;
  }
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap {
  position: relative;
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap.inactive a .product_wrap .menu_img img {
  transform: scale(0.9);
  transition: transform 800ms var(--ease-out-cubic);
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap.inactive a .product_wrap .menu_img img.rotate_90 {
  transform: rotate(90deg) scale(0.9);
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap.inactive a .product_wrap .circle_bg_inner {
  transform: scale(0.8);
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a {
  display: block;
  padding: 0;
  position: relative;
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a .product_wrap {
  min-height: 380px;
  padding: 80px 0px;
  transform-style: preserve-3d;
  transition: transform 200ms ease-out;
}
@media (max-width: 1440px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a .product_wrap {
    min-height: 340px;
  }
}
@media (max-width: 1366px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a .product_wrap {
    min-height: 300px;
    padding: 65px 0px;
  }
}
@media (max-width: 1200px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a .product_wrap {
    min-height: 280px;
    padding: 55px 0px;
  }
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a .product_wrap .menu_img {
  /*transform: translateZ(20px) scale(1) scale(0.9);*/
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a .product_wrap .menu_img img {
  width: 90%;
  transition: transform 400ms var(--ease-out-cubic);
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a .product_wrap .menu_img img.rotate_90 {
  transform: rotate(90deg);
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a .product_wrap .circle_bg {
  width: 100%;
  height: 100%;
  position: absolute;
  transform-origin: center center;
  z-index: -1;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a .product_wrap .circle_bg_inner {
  width: 240px;
  height: 240px;
  border-radius: 50%;
  background-color: var(--bg-color);
  transition: transform 800ms var(--ease-out-expo);
}
@media (-webkit-max-device-pixel-ratio: 2) and (max-width: 1540px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a .product_wrap .circle_bg_inner {
    width: 220px;
    height: 220px;
  }
}
@media (max-width: 1440px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a .product_wrap .circle_bg_inner {
    width: 200px;
    height: 200px;
  }
}
@media (max-width: 1366px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a .product_wrap .circle_bg_inner {
    width: 180px;
    height: 180px;
  }
}
@media (max-width: 1200px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a .product_wrap .circle_bg_inner {
    width: 145px;
    height: 145px;
  }
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a h2 {
  font-size: 28px;
  line-height: 40px;
  text-transform: uppercase;
  color: #000000;
  text-align: center;
  font-family: "Montserrat Bold";
  position: absolute;
  left: 50%;
  width: 100%;
  transform: translateX(-50%);
  bottom: 0;
  transition: transform 400ms var(--ease-out-cubic);
}
@media (max-width: 1440px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a h2 {
    font-size: 26px;
    line-height: 30px;
  }
}
@media (max-width: 1366px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a h2 {
    font-size: 22px;
    line-height: 20px;
  }
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a:hover {
  transform: translateY(5px);
  transition: transform 400ms var(--ease-out-expo);
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a:hover .product_wrap .menu_img img {
  transform: scale(1.15);
  transition: transform 600ms var(--ease-out-cubic) 0.1s;
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a:hover .product_wrap .menu_img img.rotate_90 {
  transform: scale(1.15) rotate(90deg);
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a:hover .product_wrap .circle_bg_inner {
  transform: translateY(10px) scale(1.1);
  transition: transform 1s var(--ease-out-quint) 0.2s;
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .menu_wrap a:hover h2 {
  transition: transform 500ms var(--ease-out-expo);
  transform: translate(-50%, -5px) scale(1.2);
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .swiper-slide:nth-child(3) a .product_wrap {
  padding-top: 0px;
}
@media (max-width: 1440px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .swiper-slide:nth-child(3) a .product_wrap {
    padding-top: 10px;
  }
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .swiper-slide:nth-child(3) a .product_wrap img {
  width: 60%;
}
@media (max-width: 1440px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .swiper-slide:nth-child(3) a .product_wrap img {
    width: 65%;
  }
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .swiper-slide:nth-child(4) a .product_wrap {
  padding-top: 50px;
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .swiper-slide:nth-child(4) a .product_wrap img {
  width: 85%;
}
@media (max-width: 1440px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .swiper-slide:nth-child(4) a .product_wrap img {
    width: 90%;
  }
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .swiper-slide:nth-child(5) a .product_wrap {
  padding-top: 40px;
}
header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .swiper-slide:nth-child(5) a .product_wrap img {
  width: 85%;
}
@media (max-width: 1440px) {
  header .nav-menu .left_menu ul.main_ul li .main_submenu.slide_menu .swiper-slide:nth-child(5) a .product_wrap img {
    width: 90%;
  }
}
header .nav-menu .left_menu ul.main_ul li.active a {
  color: var(--black-80pc);
  transition: all 0.4s ease-in-out;
}
header .nav-menu .left_menu ul.main_ul li.active .main_submenu {
  pointer-events: auto;
  opacity: 1;
  transform: translateY(30px);
  visibility: visible;
  transition: all 0.4s var(--ease-out-cubic);
}
@media (max-width: 1440px) {
  header .nav-menu .left_menu ul.main_ul li.active .main_submenu {
    transform: translateY(20px);
  }
}
@media (max-width: 1200px) {
  header .nav-menu .left_menu ul.main_ul li.current {
    background-color: var(--white-pure);
    border: 2px solid var(--white-pure);
    border-radius: 25px;
    padding: 3px 10px;
  }
  header .nav-menu .left_menu ul.main_ul li.current a {
    color: var(--black-80pc);
    transition: all 0.2s ease-out;
  }
}
header .nav-menu .right_menu ul {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  grid-gap: 15px;
}
@media (max-width: 1200px) {
  header .nav-menu .right_menu ul {
    grid-gap: 10px;
