/**
* Custom scroll-bar styles
*/
/* Chrome, Edge, Safari */
body::-webkit-scrollbar, .numbers-details::-webkit-scrollbar, .navigation::-webkit-scrollbar {
  width: var(--scrollbar-width);
}

body::-webkit-scrollbar-thumb, .navigation::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-thumb);
  border: calc(var(--scrollbar-width) / 4) solid var(--white-pure);
  border-radius: 0.7rem;
}

body::-webkit-scrollbar-thumb:hover, .navigation::-webkit-scrollbar-thumb:hover {
  background-color: var(--scrollbar-thumb-hover);
}

body::-webkit-scrollbar-track, .navigation::-webkit-scrollbar-track {
  background-color: var(--scrollbar-track);
  border-radius: 0.7rem;
}

/* Browsers without `::-webkit-scrollbar-*` support */
@supports not selector(::-webkit-scrollbar) {
  html, .navigation {
    scrollbar-width: thin;
    scrollbar-color: var(--scrollbar-thumb) var(--scrollbar-track);
  }
}
/* Chrome, Edge, Safari */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat Regular";
}

:root {
  scroll-behavior: auto !important;
}

body {
  width: 100%;
  min-height: 100%;
