    font-size: 30px;
    line-height: 26px;
    margin-bottom: 45px;
  }
}
@media (max-width: 1366px) {
  .profile-page .popup-container .popup-content h1 {
    font-size: 26px;
    line-height: 20px;
    margin-bottom: 40px;
  }
}
@media (max-width: 576px) {
  .profile-page .popup-container .popup-content h1 {
    font-size: 24px;
    line-height: 16px;
    margin-bottom: 34px;
  }
}
@media (max-width: 415px) {
  .profile-page .popup-container .popup-content h1 {
    font-size: 22px;
    line-height: 8px;
  }
}
.profile-page .popup-container:target {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
  z-index: 2;
}